<script>
export default {
  data() {
    return {
      time: "Morning",
      checkList: [],
      status: 1,
    };
  },
  methods: {
    openBook(m) {
      if (m === 1) {
        window.open(
          "https://www.bibebook.com/files/ebook/libre/V2/hugo_victor_-_notre-dame_de_paris.pdf"
        );
      }
      if (m === 2) {
        window.open(
          "http://files.hostgator.co.in/hostgator253199/file/isaacson2011stevejobs.pdf"
        );
      }
      if (m === 3) {
        window.open(
          "https://s3.amazonaws.com/earn1k/bonus/I-Will-Teach-You-To-Be-Rich_Chapter1.pdf"
        );
      }
      if (m === 4) {
        window.open(
          "https://www.ysk-books.com/app/books/Think%20Again:%20The%20Power%20of%20Knowing%20What%20You%20Don't%20Know.pdf"
        );
      }
    },
    home() {
      this.$router.push("/site/index");
    },
    surGo(m) {
      if (m === 1) {
        window.open("https://goo.gl/maps/Yk5vzexBcr6FAKjs5");
      }
      if (m === 2) {
        window.open("https://g.page/wholy-bagel-austin?share");
      }
      if (m === 3) {
        window.open("https://goo.gl/maps/fe4fH41sSdNgPekd7");
      }
      if (m === 4) {
        window.open("https://goo.gl/maps/UvZzXRCU8SgWgkRr8");
      }
      if (m === 5) {
        window.open("https://goo.gl/maps/3XX4rHpymBKbdNk59");
      }
      if (m === 6) {
        window.open("https://g.page/AustinEmergencyCenter_FarWest?share");
      }
    },
    go() {
      let main = document.getElementsByClassName("main")[0];
      main.scrollTop = 0;
      this.status = 2;
    },
    setTime() {
      let date = new Date();

      if (date.getHours() >= 0 && date.getHours() < 12) {
        this.time = "Morning";
      } else if (date.getHours() >= 12 && date.getHours() < 18) {
        this.time = "Afternoon";
      } else {
        this.time = "Night";
      }
      console.log(this.time);
    },
    openLinkedin() {
      window.open("http://www.linkedin.com/in/rayvec");
    },
  },
  mounted() {
    this.setTime();
  },
};
</script>

